/* Base sizing */
html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #0a0c11;
  color: #e7e7e7;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

:root {
  --panel-bg: rgba(255,255,255,0.06);
  --panel-border: rgba(255,255,255,0.14);
  --text-muted: rgba(255,255,255,0.85);
  --text-dim: rgba(255,255,255,0.6);
  --accent: #5aa8ff;
  --card-bg: #0b0e13;
  --shadow: 0 10px 30px rgba(0,0,0,0.35);
  --radius: 16px;
  --radius-sm: 12px;
}

.page-root {
  height: 100vh;
  margin: 0;
  display: flex;
  align-items: stretch;
}

.app-root {
  position: relative;
  flex: 1;
  height: 92vh;
  max-width: 1280px;
  margin: 2vh auto;
  background: var(--card-bg);
  border-radius: var(--radius);
  overflow: hidden;
  box-shadow: var(--shadow);
}

.canvas-wrap { position: relative; width: 100%; height: 100%; }

/* Overlay (linkes Bedienpanel) */
.overlay {
  position: absolute;
  top: 12px;
  left: 12px;
  right: 12px;
  z-index: 20;
  display: flex;
  justify-content: flex-start;
}
.panel {
  background: var(--panel-bg);
  border: 1px solid var(--panel-border);
  border-radius: var(--radius-sm);
  padding: 12px 14px;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  max-width: 420px;
}
.panel-header { display: flex; align-items: center; justify-content: space-between; gap: 12px; }
.panel-title { margin: 0; font-size: 16px; font-weight: 600; color: #fff; letter-spacing: 0.2px; }

.btn {
  appearance: none;
  border: 1px solid var(--panel-border);
  background: rgba(255,255,255,0.08);
  color: #fff;
  border-radius: 10px;
  padding: 8px 12px;
  cursor: pointer;
  transition: background 150ms ease, transform 50ms ease;
}
.btn:hover { background: rgba(255,255,255,0.14); }
.btn:active { transform: translateY(1px); }

.control { margin-top: 12px; }
.control-row { display: flex; gap: 16px; flex-wrap: wrap; }
.label { display: block; color: var(--text-muted); font-size: 13px; margin-bottom: 6px; }
.hint { color: var(--text-dim); font-size: 12px; margin-top: 6px; }

/* Slider */
.slider { width: 100%; }
.slider, .slider::-webkit-slider-runnable-track, .slider::-webkit-slider-thumb { -webkit-appearance: none; }
.slider::-webkit-slider-runnable-track { height: 6px; background: rgba(255,255,255,0.18); border-radius: 6px; }
.slider::-moz-range-track { height: 6px; background: rgba(255,255,255,0.18); border-radius: 6px; }
.slider::-webkit-slider-thumb { width: 16px; height: 16px; border-radius: 50%; margin-top: -5px; background: #fff; border: 1px solid var(--panel-border); box-shadow: 0 1px 4px rgba(0,0,0,0.25); }
.slider::-moz-range-thumb { width: 16px; height: 16px; border-radius: 50%; background: #fff; border: 1px solid var(--panel-border); box-shadow: 0 1px 4px rgba(0,0,0,0.25); }

.checkbox { display: inline-flex; align-items: center; gap: 8px; color: var(--text-muted); font-size: 13px; cursor: pointer; user-select: none; }
.checkbox input { accent-color: var(--accent); width: 16px; height: 16px; }

.note { color: var(--text-dim); font-size: 12px; margin-top: 8px; line-height: 1.4; }

/* Legende unten rechts */
.legend {
  position: absolute;
  right: 12px;
  bottom: 12px;
  background: var(--panel-bg);
  border: 1px solid var(--panel-border);
  border-radius: var(--radius-sm);
  padding: 10px 12px;
  color: var(--text-muted);
  font-size: 12px;
  line-height: 1.5;
  max-width: 260px;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  box-shadow: var(--shadow);
  z-index: 10;
}
.legend-title { color: #fff; font-weight: 600; margin-bottom: 6px; }

/* Labels Ã¼ber Planeten */
.label-bubble {
  padding: 2px 6px;
  border-radius: 999px;
  font-size: 10px;
  line-height: 1;
  background: rgba(0,0,0,0.55);
  color: #fff;
  border: 1px solid rgba(255,255,255,0.15);
  white-space: nowrap;
  box-shadow: 0 1px 6px rgba(0,0,0,0.35);
}

/* Info-Panel (rechts oben) */
.info-panel { z-index: 30; position: absolute; top: 12px; right: 12px; background: var(--panel-bg); border: 1px solid var(--panel-border); border-radius: var(--radius-sm); padding: 12px 14px; color: var(--text-muted); width: 280px; backdrop-filter: blur(6px); -webkit-backdrop-filter: blur(6px); box-shadow: var(--shadow); }
.info-header { display: flex; align-items: center; justify-content: space-between; margin-bottom: 8px; }
.info-title { color: #fff; font-weight: 600; font-size: 16px; }
.info-close { appearance: none; border: 1px solid var(--panel-border); background: rgba(255,255,255,0.08); color: #fff; border-radius: 8px; width: 28px; height: 28px; line-height: 26px; text-align: center; cursor: pointer; }
.info-grid { display: grid; grid-template-columns: 1fr; gap: 8px; font-size: 13px; }
.info-row { display: flex; align-items: center; justify-content: space-between; border-bottom: 1px dashed rgba(255,255,255,0.12); padding-bottom: 6px; }
.info-row:last-child { border-bottom: none; padding-bottom: 0; }
.info-thumb { width: 100%; aspect-ratio: 2 / 1; border-radius: 10px; overflow: hidden; margin-bottom: 10px; border: 1px solid var(--panel-border); }
.info-thumb img { width: 100%; height: 100%; object-fit: cover; display: block; }
